import React, { useReducer } from 'react'
import { toast } from 'react-toastify';
import useLocalStorage from '../../hooks/useLocalStorage';

export const useUserService=()=>{
    
    const [data,setData]=useLocalStorage('usersList',[{"id":1612265181259,"name":"revan","email":"revan@gmail.com","password":"123","address":"shj","roles":[{"value":"store","label":"Store","id":"3"}]},{"id":1612265224441,"name":"uttham","email":"uttham@gmail.com","password":"123","address":"kjsdsad","roles":[{"value":"siteengineer","label":"Site Engineer","id":"2"}]},{"id":1612265253524,"name":"pramod","email":"pramod@gmail.com","password":"123","address":"asd","roles":[{"value":"admin","label":"Admin","id":"1"}]}]);

    const add=(v)=>{
        toast.success("Submitted Successfully")
        setData([...(data||[]),{id:Date.now(),...v}])}

    const update=(v)=>{
        toast.success("Updated Successfully")
       // alert(JSON.stringify(data))
       //const data1=data?.map(item1=>item1.id==v.id?v:item1)
       setData(data=>data.map(item1=>item1.id==v.id?v:item1))
    }
    
    const deletec=(v)=>{
        toast.error("deleted Successfully")
       // alert(JSON.stringify(data))
       //const data1=[...(data||[])].filter(item1=>item1.id!=v.id)
       setData(data=>data.filter(item1=>item1.id!=v.id))
        }
 const actions={add,update,deletec}
return [data||[],actions]
}