
import React from 'react'
import { useForm } from '../../hooks/useForm'
import {controls} from '../../components/Inputs'
import { Box, Button } from '@material-ui/core'

export default ({value,addorEdit}) =>{
    const initialValues={name:'',email:'',password:'',address:'',roles:[]}
const {values,handleInputChange,resetForm,setValue}=useForm(( Object.keys(value).length==0)?initialValues:value,false,{})
const roleOptions=[{value:'admin',label:'Admin',id:'1'},{value:'siteengineer',label:'Site Engineer',id:'2'},{value:'store',label:'Store',id:'3'}]

     const handleSubmit=(e)=>{
         e.preventDefault();
         //alert(JSON.stringify(values))
        addorEdit(values)
        resetForm(initialValues);
     }
    return (
        <form onSubmit={handleSubmit}>
            <controls.MyInput1 label='name' name={'name'}  value={values?.name} onChange={handleInputChange}/>
            <controls.MyInput1 label='email' name={'email'}  value={values?.email} onChange={handleInputChange}/>
            <controls.MyInput1 label='password' name={'password'}  value={values?.password} onChange={handleInputChange}/>
            <controls.MyInput1 label='address' name={'address'}  value={values?.address} onChange={handleInputChange}/>
            <controls.MySelect label='Roles' value={values?.roles}   options={roleOptions} isMulti={true} onChange={v=>setValue('roles',v)}/>
<Box textAlign='center'><controls.Submit text={'submit'}/>

 </Box>
        </form>
    )
}

